import { Component, OnInit } from '@angular/core';
import {SubFoodAdminService} from '../../../services/sub-food-admin.service';
import {ToastController} from '@ionic/angular';
import {ActivatedRoute, Router} from '@angular/router';
import {DataUrl, DOC_ORIENTATION, NgxImageCompressService, UploadResponse} from 'ngx-image-compress';


@Component({
  selector: 'app-add-sub-food',
  templateUrl: './add-sub-food.component.html',
  styleUrls: ['./add-sub-food.component.scss'],
})
export class AddSubFoodComponent implements OnInit {
  id: number;
  title: string;
  imgResultAfterResizeMax: DataUrl = '';

  constructor(
    private subFoodService: SubFoodAdminService,
    private toastCtrl: ToastController,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private imageCompress: NgxImageCompressService,
  ) {
    this.id = +this.activatedRoute.snapshot.paramMap.get('id');
  }

  ngOnInit() {

  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  subFood: { food: number; title: string; price: number;pic: string;  description: string; published: boolean } = {
    food: 0,
    title: '',
    price: null,
    pic: '',
    description: '',
    published: true,
  };


  async confirmationEdit() {
    const toast = await this.toastCtrl.create({
        message: 'Updated successfully!',
        mode: 'ios',
        duration: 1000,
        position: 'bottom',
        color: 'success'
      }
    );
    toast.present();
    // @ts-ignore
    this.router.navigate(['admin/listing']);
  }


  uploadAndReturnWithMaxSize() {
    return this.imageCompress.uploadAndGetImageWithMaxSize(1, true).then(
      (result: DataUrl) => {
        this.imgResultAfterResizeMax = result;
        console.log('-->',result);
      },
      (result: string) => {
        console.error(
          'The compression algorithm didn\'t succed! The best size we can do is',
          this.imageCompress.byteCount(result),
          'bytes'
        );
        this.imgResultAfterResizeMax = result;
      }
    );
  }
  saveFood(): void {
    const data = {
      title: this.subFood.title,
      price: this.subFood.price,
      pic: this.imgResultAfterResizeMax,
      description: this.subFood.description,
      published: this.subFood.published,
    };

    this.subFoodService.create(this.id,data)
      .subscribe(
        response => {
          console.log(response);
          //this.router.navigate(['/admin/listing']);
          window.location.reload();
        },
        error => {
          console.log(error);
        });
    window.history.back();
  }

}
